@import '../../styles/utils/mixins.scss';

.container {
  min-height: 100vh;
  max-width: 1136px;
  margin: 90px auto 80px;
  background-color: #0f1121;
}

.title {
  text-align: start;
  font-family: 'Mont', sans-serif;
  font-style: normal;
  font-weight: 800;
  font-size: 48px;
  line-height: 56px;

  letter-spacing: -0.01em;

  color: #f1f2f9;
}

.phones_container {
  max-width: 1138px;
  display: flex;
  flex-wrap: wrap;
  gap: 16px;
  margin: 24px 0 40px;
}

.sub_title {
  padding-bottom: 40px;
  padding-top: 8px;
  font-family: 'Mont', sans-serif;
  font-style: normal;
  font-weight: 600;
  font-size: 14px;
  line-height: 21px;
  color: #75767F;
}

@include tablet {
  .container {
    padding-left: 33px;
  }
}

@include mobile {
  .container {
    padding-left: 24px;
  }
}
