@import '../../styles/utils/mixins.scss';

.list {
  overflow: hidden;
  margin-top: 80px;

  @include tablet {
    margin-top: 64px;
    overflow: visible;
  }

  @include mobile {
    margin-top: 56px;
    overflow: visible;
  }
}

.wrapper {
  display: flex;
  justify-content: space-between;
  user-select: none;

  @media (max-width: 380px) {
    max-width: 136px;
  }
}

.arrow__container {
  display: flex;
  gap: 16px;
  justify-content: flex-end;
  margin-bottom: 28px;
  button:first-child {
    transform: rotateY(180deg);
  }
}

.title {
  color: var(--white);
  letter-spacing: -0.01em;
  font-size: 32px;
  line-height: 41px;
  font-family: 'Mont-Bold', sans-serif;

  @include mobile {
    font-size: 22px;
    line-height: 31px;
  }
}
