@import '../../styles/utils/mixins.scss';

.button {
  transition: background-color, filter, 0.3s ease;
  background-color: var(--accent);
  border: 0;
  color: var(--white);
  padding: 10px 0 10px;
  flex-grow: 1;
  font-family: 'Mont-Semibold', sans-serif;

  @include desktop {
    &:hover {
      filter: brightness(120%);
    }
  }
}

.pressed {
  background: var(--surface-two);
}
